.sendForm {
    display: flex;
    justify-content: space-between;
    padding: 0 50px;
    /*flex-direction: column;*/
    align-items: flex-end;
    outline: black solid thin;
    font-family: 'Montserrat', serif;
}

.textarea {
    /*outline: none;*/
    /*-moz-appearance: none;*/
    /*border: 1px solid #999;*/
    width: 970px;
    min-height: 40px;
    /*background-color:  #F5F7FB;*/
    color: #000000;
    margin-left: 15px;
    position: relative;
    padding: 10px;
    /*border-radius: 10px 10px 10px 10px;*/
    background: #F5F7FB;
    box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03), 0px 1px 2px rgba(29, 33, 38, 0.1);
    border-radius: 20px;
    resize: none;
}

.button {
    width: 120px;
    height: 40px;
    background: #0066CC;
    border-radius: 20px;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: #FFFFFF;
}

.button:hover {
    background: blue;
}

.button:active {
    background: lawngreen;
}
